$border-color: #764a34;

.checkout-item-container {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid $border-color;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;

    .image-container {
        width: 20%;
        padding-right: 15px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .name, .quantity, .price {
        width: 25%;
    }

    .quantity {
        display: flex;

        .arrow {
            cursor: pointer;
        }

        .value {
            margin: 0 10px;
        }
    }

    .remove-button {
        padding-left: 12px;
        cursor: pointer;
    }
}