.cart-item-container {
    width: 100%;
    display: flex;
    height: 80px;
    margin-bottom: 16px;

    img {
        width: 32%;
    }

    .item-details {
        width: 68%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        padding: 10px 20px;

        .name {
            font-size: 16px;
        }
    
        .price {
            font-size: 16px;
        }
    }
}